// Dialog Window Behavior
document.addEventListener("DOMContentLoaded", () => {
  const mask = document.querySelector('.oui-dialog-mask');
  let activeDialog = null;
  const preventTouch = (e) => e.preventDefault();
  let startY = 0;
  function blockInteractions() {
    document.body.style.pointerEvents = 'none';
    document.body.style.userSelect = 'none';
    document.body.style.overflow = 'hidden';
    document.documentElement.style.pointerEvents = 'none';
    document.documentElement.style.userSelect = 'none';
  }
  function unblockInteractions() {
    document.body.style.pointerEvents = '';
    document.body.style.userSelect = '';
    document.body.style.overflow = '';
    document.documentElement.style.pointerEvents = '';
    document.documentElement.style.userSelect = '';
  }
  const onDialogTouchStart = (e) => {
    startY = e.touches[0].clientY;
  };
  const onDialogTouchMove = (e) => {
    if (!activeDialog) return;
    const dialog = activeDialog;
    const scrollTop = dialog.scrollTop;
    const scrollHeight = dialog.scrollHeight;
    const offsetHeight = dialog.offsetHeight;
    const currentY = e.touches[0].clientY;
    const isScrollingDown = currentY > startY;
    const isScrollingUp = currentY < startY;
    if (
      (scrollTop === 0 && isScrollingDown) ||
      (scrollTop + offsetHeight >= scrollHeight && isScrollingUp)
    ) {
      e.preventDefault();
    }
    startY = currentY;
  };
  const openDialog = (dialog) => {
    dialog.classList.add('show');
    mask.classList.add('show');
    activeDialog = dialog;
    document.body.classList.add('no-scroll');
    document.addEventListener('touchmove', preventTouch, { passive: false });
    dialog.addEventListener('touchstart', onDialogTouchStart, { passive: false });
    dialog.addEventListener('touchmove', onDialogTouchMove, { passive: false });
  };
  const closeDialog = () => {
    if (!activeDialog) return;
    blockInteractions();
    activeDialog.classList.remove('show');
    activeDialog.classList.add('hide');
    mask.classList.remove('show');
    document.body.classList.remove('no-scroll');
    document.removeEventListener('touchmove', preventTouch);
    activeDialog.removeEventListener('touchstart', onDialogTouchStart);
    activeDialog.removeEventListener('touchmove', onDialogTouchMove);
    const handleTransitionEnd = (event) => {
      if (event.target === activeDialog && activeDialog.classList.contains('hide')) {
        activeDialog.removeEventListener('transitionend', handleTransitionEnd);
        setTimeout(() => {
          activeDialog.classList.remove('hide');
          activeDialog = null;
          unblockInteractions();
        }, 5);
      }
    };
    activeDialog.addEventListener('transitionend', handleTransitionEnd);
  };
  document.querySelectorAll('[data-dialog-target]').forEach(button => {
    button.addEventListener('click', () => {
      const targetId = button.getAttribute('data-dialog-target');
      const dialog = document.getElementById(targetId);
      if (dialog) openDialog(dialog);
    });
  });
  document.querySelectorAll('.close-dialog-btn').forEach(closeBtn => {
    closeBtn.addEventListener('click', closeDialog);
  });
  mask.addEventListener('click', (e) => {
    const closingAppDialog = document.getElementById('closing_app_dialog');
    if (closingAppDialog && closingAppDialog.classList.contains('show')) {
      e.preventDefault();
      e.stopPropagation();
      return false;
    }
  });
});
// Dialog Glow Effect Behavior
document.addEventListener('DOMContentLoaded', () => {
  const dialogGlowMap = new WeakMap();
  const ensureDialogGlows = (dialog) => {
    if (!dialog) return null;
    if (dialogGlowMap.has(dialog)) return dialogGlowMap.get(dialog);
    const desc = dialog.querySelector('.oui-dialog-description');
    if (!desc) return null;
    let topGlow = dialog.querySelector('.dialog-top-glow');
    let bottomGlow = dialog.querySelector('.dialog-bottom-glow');
    if (!topGlow) {
      topGlow = document.createElement('div');
      topGlow.className = 'dialog-top-glow';
      dialog.appendChild(topGlow); 
    }
    if (!bottomGlow) {
      bottomGlow = document.createElement('div');
      bottomGlow.className = 'dialog-bottom-glow';
      dialog.appendChild(bottomGlow);
    }
    topGlow.style.opacity = topGlow.style.opacity || '0';
    bottomGlow.style.opacity = bottomGlow.style.opacity || '0';
    const item = { desc, topGlow, bottomGlow };
    dialogGlowMap.set(dialog, item);
    return item;
  };
  const isScrollable = (el) => el && (el.scrollHeight > el.clientHeight + 1);
  const findScrollableForDialog = (dialog) => {
    if (!dialog) return null;
    const desc = dialog.querySelector('.oui-dialog-description');
    if (desc) return desc;
    return dialog;
  };
  const setGlowForDialog = (dialog, on) => {
    const g = dialogGlowMap.get(dialog) || ensureDialogGlows(dialog);
    if (!g) return;
    g.topGlow.style.opacity = on ? '1' : '0';
    g.bottomGlow.style.opacity = on ? '1' : '0';
  };
  const updateGlow = () => {
    requestAnimationFrame(() => {
      const active = document.querySelector('.oui-dialog.show');
      if (!active) return setGlowForDialog(active, false);
      const scrollable = findScrollableForDialog(active);
      setGlowForDialog(active, Boolean(scrollable && isScrollable(scrollable)));
    });
  };
  updateGlow();
  const mo = new MutationObserver(updateGlow);
  document.querySelectorAll('.oui-dialog').forEach(d => mo.observe(d, { attributes: true, attributeFilter: ['class'] }));
  if ('ResizeObserver' in window) {
    const ro = new ResizeObserver(updateGlow);
    ro.observe(document.documentElement);
    document.querySelectorAll('.oui-dialog, .oui-dialog-description').forEach(el => ro.observe(el));
  }
  const domMo = new MutationObserver(updateGlow);
  document.querySelectorAll('.oui-dialog .oui-dialog-description').forEach(sc => domMo.observe(sc, { childList: true, subtree: true }));
  window.addEventListener('resize', updateGlow);
});
